.top{
    margin-top: -5px;
}

.custom-header{
    background: #0099ff;
}

.nav-item{
    user-select: none;
}

.nav-item:hover{
    cursor: pointer;
}

.nav-item > .active{
    border-bottom:1px solid #E75D7A;
}

.navbar-brand{
    user-select: none;
}

.navbar-brand:hover{
    cursor: pointer;
}

.custom-toggler .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(254,179,61, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

.custom-toggler.navbar-toggler {
    border-color: #FEB33D;
  }
